<template>
    <div>
        <label :class="isRequired?'ai-form-item-label-required':''">{{label}}</label>
        <div>
            <slot></slot>
            <div class="ai-form-item-message" v-if="validateState==='error'">{{validateMessage}}</div>
        </div>
    </div>
</template>

<script>
    import Emitter from './emitter.js';
    import schema from 'async-validator';
    export default {
        name: "ayuFormItem",
        mixins: [Emitter],
        inject: ['form'],
        props: {
            label: {
                type: String,
                default: ''
            },
            prop:{
                type: String
            },
        },
        computed:{
            fieldValue () {
                return this.form.model[this.prop];
            },
        },
        data(){
            return {
                initialValue: '', // 
                isRequired: false, // 
                validateState: '', // 
                validateMessage: '', // 
            }
        },
        methods:{
            /**
             *   required 
             */
            setRules(){
                let that = this;
                let rules = this.getRules();//
                if (rules.length) {
                    // every 
                    // some true
                    this.isRequired = rules.some(rule=>{
                        // 
                        return rule.required;
                    })
                }
                /**
                 * blur 
                 */
                this.$on('on-form-blur',that.onFieldBlur);
                /**
                 * change 
                 */
                this.$on('on-form-change',that.onFieldChange)
            },
            /**
             *  Form  rules  FormItem 
             */
            getRules () {
                let that = this;
                let rules = that.form.rules;
                rules = rules?rules[that.prop]:[];
                return [].concat(rules||[])//
            },
            /**
             * Blur 
             */
            onFieldBlur(){
                this.validation('blur')
            },
            /**
             * change 
             */
            onFieldChange(){
                this.validation('change')
            },
            /**
             *  blur  change rule 
             */
            getFilteredRule (trigger) {
                let rules = this.getRules();
                // !res.trigger 
                // filter 
                return rules.filter(res=>!res.trigger || res.trigger.indexOf(trigger)!==-1)
            },
            /**
             * 
             * @param trigger 
             * @param callback 
             */
            validation(trigger,callback=function () {}){
                // blur  change 
                let rules = this.getFilteredRule(trigger);
                // 
                if (!rules || rules.length === 0) {
                    return
                }
                // 
                // async-validator
                this.validateState = 'validating';
                var validator = new schema({[this.prop]: rules});
                // firstFields: true 
                validator.validate({[this.prop]: this.fieldValue}, { firstFields: true },(errors, fields) => {
                    this.validateState = !errors ? 'success' : 'error';
                    this.validateMessage = errors ? errors[0].message : '';
                    callback(this.validateMessage);
                });
            },
            /**
             *  form-item
             */
            resetField(){
                this.form.model[this.prop] = this.initialValue;
            }
        },
        //  Form 
        mounted(){
            //  prop
            if (this.prop) {
                this.dispatch('ayuForm','on-form-item-add', this);
                // 
                this.initialValue = this.fieldValue;
                // 
                this.setRules()
            }
        },
        //  Form 
        beforeDestroy(){
            this.dispatch('ayuForm', 'on-form-item-remove', this);
        },
    }
</script>

<style scoped>
    .ai-form-item-label-required:before{
        content: '*';
        color: red;
    }
    .ai-form-item-message {
        color: red;
    }
</style>