<template>
  <div class="ayu-checkbox-group">
    <slot></slot>
  </div>
</template>

<script>
export default {
  name: 'AyuCheckboxGroup',
  props: {
    value: {},
    disabled: {
      type: Boolean,
      default: false,
    }
  },
  model: {
    prop: "value",
    event: "select"
  },
  watch: {
    value(newval) {
      // change
      this.$emit('change', newval);
    }
  },
  methods: {
    selectItem(item) {
      const { value } = this;
      // 
      this.$emit('select', [...value, item]);
    },
    // value
    deleteItem(item) {
      this.$emit('select', this.value.filter(selectItem => selectItem !== item));
    }
  }
}
</script>
