<template>
    <form>
        <slot></slot>
    </form>
</template>

<script>
export default {
  name: "ayuForm",
  provide() {
    return {
      form: this
    };
  },
  props: {
    //  form model
    model: {
      type: Object
    },
    // 
    rules: {
      type: Object
    }
  },
  data() {
    return {
      fields: [] //  form-item
    };
  },
  created() {
    // 
    let that = this;
    this.$on("on-form-item-add", item => {
      if (item) {
        that.fields.push(item);
      }
    });
    // 
    this.$on("on-form-item-remove", item => {
      if (item.prop) {
        // prop
        that.fields.splice(that.fields.indexOf(item), 1);
      }
    });
  },
  methods: {
    /**
     * 
     */
    resetFields() {
      //resetFields
      /**
       * form-item 
       */
      this.fields.forEach(field => {
        field.resetField();
      });
    },
    /**
     *   Promise
     */
    validate(callback) {
      return new Promise(resolve => {
        /**
         * form-item 
         */
        let valid = true; // 
        let count = 0; // 
        this.fields.forEach(field => {
          //  validation 
          field.validation("", error => {
            // 
            if (error) {
              valid = false;
						}
            // form-item
            if (++count === this.fields.length) {
              resolve(valid); // then
              if (typeof callback === "function") {
                callback(valid); // 
              }
            }
          });
        });
      });
    }
  }
};
</script>
