// 
import '../examples/style/index.scss';
// 
import '../examples/icon/iconfont.js'
// 
import Button from './Button'
import Icon from './Icon'
import Carousel from './Carousel'
import CarouselItem from './Carousel-item'
import Floatinput from './FloatInput'
import Pagination from './Pagination'
import Select from './Select'
import SelectItem from './Select-option'
import MessageBox from './Message-box'
import HoverTip from './Hover-tip'
import Input from './Input'
import Message from './Message'
import Notification from './Notification'
import Row from './Row'
import Col from './Col'
import Radio from './Radio'
import RadioGroup from './Radio-group'
import Checkbox from './Checkbox'
import CheckboxGroup from './Checkbox-group'
import Circle from './Circle'
import InputNumber from './InputNumber'
import Form from './Form'
import FormItem from './Form-item'

// 
const components = [
  Button,
  Icon,
  Carousel,
  CarouselItem,
  Floatinput,
  Pagination,
  Select,
  SelectItem,
  HoverTip,
  Input,
  Row,
  Col,
  Radio,
  RadioGroup,
  Checkbox,
  CheckboxGroup,
  Circle,
  InputNumber,
  Form,
  FormItem
]

//  install  Vue  use 
const install = function (Vue) {
  // 
  if (install.installed) return
  // 
  components.map(component => Vue.component(component.name, component))
  Vue.prototype.$alert = MessageBox.alert
  Vue.prototype.$confirm = MessageBox.confirm
  Vue.prototype.$message = Message.$message
  Vue.prototype.$closeMessage = Message.$closeMessage
  Vue.prototype.$notify = Notification.$notify
}

// 
if (typeof window !== 'undefined' && window.Vue) {
  install(window.Vue)
}

export default {
  //  install Vue.use() 
  install,
  // 
  Button,
  Icon,
  Carousel,
  CarouselItem,
  Floatinput,
  Pagination,
  Select,
  SelectItem,
  MessageBox,
  HoverTip,
  Input,
  Message,
  Notification,
  Row,
  Col,
  Radio,
  RadioGroup,
  Checkbox,
  CheckboxGroup,
  Circle,
  InputNumber,
  Form,
  FormItem
}
