<template>
<transition name="fade">
  <div class="ayu-message-box_wrapper" v-if="visible">
    <div class="ayu-message-box">
      <div class="ayu-message-box_header">
        <div class="ayu-message-box_title">{{title}}</div>
        <div class="ayu-message-box_close" v-if="showCloseBtn" @click="handleClick('close')">
          <ayu-icon icon-class="close"></ayu-icon>
        </div>
      </div>
      <div class="ayu-message-box_content" v-html="content"></div>
      <div class="ayu-message-box_footer">
        <ayu-button @click.stop="handleClick('cancel')" v-if="cancelButtonText && type !== 'alert'"></ayu-button>
        <ayu-button @click.stop="handleClick('confirm')" type="primary" v-if="cancelButtonText"></ayu-button>
      </div>
    </div>
  </div>
</transition>
</template>

<script>
import box from '../../mixins/box.js';
export default {
  name: 'AyuMessageBox',
  mixins: [box],
  props: {
    title: { type: String, default: '' },
    content: { type: String, default: '', required: true },
    type: { type: String, default: '' },
    confirmButtonText: { type: String, default: ''},
    cancelButtonText: { type: String, default: ''},
    showCloseBtn: { type: Boolean, default: true }
  },
  methods: {
    handleClick(clickType) {
      // option.onXXX
      this.$emit(clickType)
      // messageBox
      this.close();
    }
  },
}
</script>
