<template>
   <input type="text"
          @input="handleInput"
          @blur="handleBlur"
          :value="defaultValue"
   >
</template>

<script>
   import Emitter from './emitter.js'
   export default {
       name: "aiInput",
       mixins: [Emitter],
       props: {
           value: {
               type: [String, Number],
               default: ''
           }
       },
       data(){
           return {
               defaultValue: this.value
           }  
       },
       watch:{
           value (val) {
               this.defaultValue = val;
           }
       },
       methods:{
           /**
            * change 
            * @param event
            */
           handleInput(event){
               // model 
               this.defaultValue = event.target.value;
               // vue  return 
               this.$emit('input',event.target.value);
               //  ayuFormItem 
               this.dispatch('ayuFormItem','on-form-change',event.target.value)
           },
           /**
            * blur 
            * @param event
            */
           handleBlur(event){
               // vue  return 
               this.$emit('blur',event.target.value);
               //  ayuFormItem 
               this.dispatch('ayuFormItem','on-form-blur',event.target.value)
           }
       }
   }
</script>
