<template>
  <div class="ayu-form-item">
    <label 
      class="ayu-form-item__label" 
      :class="isRequired ? 'ayu-form-item__label-required' : 'ayu-form-item__label-noRequired' "
      v-if="label || $slots.label"
      :style="labelStyle">
      {{label}}
    </label>
    <div class="ayu-form-item__content" :style="contentStyle">
      <slot></slot>
      <transition name="fade">
        <div class="ayu-form-item__message" v-if="validateState==='error'">{{validateMessage}}</div>
      </transition>
    </div>
  </div>
</template>

<script>
import Emitter from "../../mixins/emitter.js";
import schema from "async-validator";
export default {
  name: "ayuFormItem",
  mixins: [Emitter],
  inject: ["form"],
  props: {
    label: {
      type: String,
      default: ""
    },
    prop: {
      type: String
    },
    rules: {
      type: Array,
    },
    labelWidth: String,
  },
  computed: {
    // item
    fieldValue() {
      return this.form.model[this.prop];
    },
    // labelstyle
    labelStyle() {
      const obj = {};
      if (this.form.labelPosition === 'top') return obj;
      const labelWidth = this.labelWidth || this.form.labelWidth;
      if (labelWidth) {
        obj.width = labelWidth;
      }
      obj.textAlign = this.form.labelPosition;
      obj.float = 'left';
      return obj;
    },
    // style
    contentStyle() {
      const obj = {};
      if (this.form.labelPosition === 'top') return obj;
      const labelWidth = this.labelWidth || this.form.labelWidth;
      obj.marginLeft = labelWidth;
      return obj;
    }
  },
  data() {
    return {
      initialValue: "", // 
      isRequired: false, // 
      validateState: "", // 
      validateMessage: "" // 
    };
  },
  methods: {
    /**
     *   required 
     */
    setRules() {
      let that = this;
      let rules = this.getRules(); //
      if (rules.length) {
        // every 
        // some true
        this.isRequired = rules.some(rule => {
          // 
          return rule.required;
        });
      }
      /**
       * blur 
       */
      this.$on("on-form-blur", that.onFieldBlur);
      /**
       * change 
       */
      this.$on("on-form-change", that.onFieldChange);
    },
    /**
     *  Form  rules  FormItem 
     */
    getRules() {
      let that = this;
      let rules = that.form.rules;
      rules = this.rules ? this.rules : rules[that.prop];
      return [].concat(rules || []); //
    },
    /**
     * Blur 
     */
    onFieldBlur() {
      this.validation("blur");
    },
    /**
     * change 
     */
    onFieldChange() {
      this.validation("change");
    },
    /**
     *  blur  change rule 
     */
    getFilteredRule(trigger) {
      let rules = this.getRules();
      // !res.trigger 
      // filter 
      return rules.filter(
        res => !res.trigger || res.trigger.indexOf(trigger) !== -1 || !this.isContained(res.trigger, trigger)
      );
    },
    isContained(a, b) {
      if(!(a instanceof Array) || !(b instanceof Array)) return false;
      return true;
    },
    /**
     * 
     * @param trigger 
     * @param callback 
     */
    validation(trigger, callback = function() {}) {
      // blur  change 
      let rules = this.getFilteredRule(trigger);
      // 
      if (!rules || rules.length === 0) {
        return;
      }
      // 
      // async-validator
      this.validateState = "validating";
      var validator = new schema({ [this.prop]: rules });
      // firstFields: true 
      validator.validate(
        { [this.prop]: this.fieldValue },
        { firstFields: true },
        (errors, fields) => {
          this.validateState = !errors ? "success" : "error";
          this.validateMessage = errors ? errors[0].message : "";
          callback(this.validateMessage);
        }
      );
    },
    /**
     *  form-item
     */
    resetField() {
      this.form.model[this.prop] = this.initialValue;
      this.validateState = "validating";
    }
  },
  //  Form 
  mounted() {
    //  prop
    if (this.prop) {
      this.dispatch("ayuForm", "on-form-item-add", this);
      // 
      this.initialValue = this.fieldValue;
      // 
      this.setRules();
    };
  },
  //  Form 
  beforeDestroy() {
    this.dispatch("ayuForm", "on-form-item-remove", this);
  }
};
</script>
